  color: #ffffff


.black-bg
  background-color: #262626


.panel-primary
  border-color: $navy


.panel-primary > .panel-heading
  background-color: $navy
  border-color: $navy


.panel-success
  border-color: $blue


.panel-success > .panel-heading
