  border-color: $yellow
  color: #ffffff


.panel-danger
  border-color: $red


.panel-danger > .panel-heading
  background-color: $red
  border-color: $red
  color: #ffffff


.progress-bar
  background-color: $navy


.progress-small, .progress-small .progress-bar
  height: 10px


.progress-small, .progress-mini
  margin-top: 5px


.progress-mini, .progress-mini .progress-bar
  height: 5px
  margin-bottom: 0px


.progress-bar-navy-light
  background-color: #3dc7ab


.progress-bar-success
  background-color: $blue


.progress-bar-info
  background-color: $lazur


.progress-bar-warning
  background-color: $yellow


.progress-bar-danger
  background-color: $red


.panel-title
  font-size: inherit


.jumbotron
  border-radius: 6px
  padding: 40px


.jumbotron h1
  margin-top: 0


/* COLORS */

.text-navy
  color: $navy


.text-primary
  color: inherit


.text-success
  color: $blue


.text-info
  color: $lazur


.text-warning
  color: $yellow


.text-danger
  color: $red


.text-muted
