  height: 400px

.gridStyle2
  border: 1px solid rgb(212, 212, 212)
  width: 500px
  height: 300px

.ngH eaderCell
  border-right: none
  border-bottom: 1px solid #e7eaec

.ngCell
  border-right: none

.ngTopPanel
  background: #F5F5F6

.ngRow.even
  background: #f9f9f9

.ngRow.selected
