.btn-large-dim
  width: 90px
  height: 90px
  font-size: 42px

button.dim
  display: inline-block
  color: #fff
  text-decoration: none
  text-transform: uppercase
  text-align: center
  padding-top: 6px
  margin-right: 10px
  position: relative
  cursor: pointer
  border-radius: 5px
  font-weight: 600
  margin-bottom: 20px !important

button.dim:active
  top: 3px

button.btn-primary.dim
  box-shadow: inset 0px 0px 0px darken($navy, 6%), 0px 5px 0px 0px darken($navy, 6%), 0px 10px 5px #999

button.btn-primary.dim:active
  box-shadow: inset 0px 0px 0px darken($navy, 6%), 0px 2px 0px 0px darken($navy, 6%), 0px 5px 3px #999

button.btn-default.dim
  box-shadow: inset 0px 0px 0px darken($dark-gray, 6%), 0px 5px 0px 0px darken($dark-gray, 6%), 0px 10px 5px #999

button.btn-default.dim:active
